
import { hillo, IKDataEntity } from 'innerken-utils'
import ConsumeType from './ConsumeType'

const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
    formConfig: {
      requiredNew: false,
    },
  },

  consumeTypeId: {
    displayName: '自助餐类型',
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: ConsumeType.getList,
      },
    },
  },

  // buffetPrice: {
  //   displayName: '自助餐价格',
  //   formConfig: {
  //     requiredNew: false,
  //       type: {
  //         selectItems: BuffetType.getList,
  //       },
  //   },
  //   form: false,
  // },
  totalRound: {
    displayName: '自助餐轮数',
    formConfig: {
      default: '',
      required: false,
      rule: [
        v => /^[0-9]\d*$/.test(v) || 'Bitte geben Sie die richtige Zahl ein',
      ],
    },
  },
  roundTime: {
    displayName: '自助餐每轮时长（秒）',
    formConfig: {
      default: '',
      required: false,
      rule: [
        v => /^[0-9]\d*$/.test(v) || 'Bitte geben Sie die richtige Zahl ein',
      ],
    },
  },
  maxDineTime: {
    displayName: '最长时间（秒）',
    formConfig: {
      default: '',
      required: false,
      rule: [
        v => /^[0-9]\d*$/.test(v) || 'Bitte geben Sie die richtige Zahl ein',
      ],
    },
  },
  roundDishCount: {
    displayName: '成人每轮点菜数',
    formConfig: {
      default: '',
      required: false,
      rule: [
        v => /^[0-9]\d*$/.test(v) || 'Bitte geben Sie die richtige Zahl ein',
      ],
    },
  },
  childRoundDishCount: {
    displayName: '儿童每轮点菜数',
    formConfig: {
      default: '',
      required: false,
      rule: [
        v => /^[0-9]\d*$/.test(v) || 'Bitte geben Sie die richtige Zahl ein',
      ],
    },
  },
}

const config = {
  add: function (item) {
    return hillo.post('ConsumeType.php?op=addJpBuffetSetting', {
      ...item,
      langs: JSON.stringify(item.langs),
    }, {
      showLoading: true,
    })
  },
  edit: function (item) {
    return hillo.postWithUploadFile('ConsumeType.php?op=updateJpBuffetSetting', {
      ...item,
      langs: JSON.stringify(item.langs),
    }, {
      showLoading: true,
    })
  },
  remove: function (id) {
    return hillo.post('ConsumeType.php?op=deleteJpBuffetSetting', { id: id })
  },

  load: async function (filter) {
    return (await hillo.get('ConsumeType.php?op=showAllBuffetSetting', {
      ...filter,
    }))
      .content.map(function (item) {
        return IKDataEntity.parseDataForEntity(item, entity)
      })
  },
}

export default IKDataEntity.ModelFactory(entity, config)
